<template>
	<div>
		<div>
			<span @click="dragend(3,{
						name: 'echarts4',
						id: 4
					})">9999999</span>
			<span @click="dragend(0,{
								name: 'echarts1',
								id: 1
							})">1111</span>
			<span @click="dragend(2,{
												name: 'echarts3',
												id: 3
											})">333333</span>
		</div>
		<transition-group name="flip-list" class="vegroup">
			<div style="height: 200px;width: 100%;" v-for="(item,index) in menuarr" :key="item.id" value-key="item.id">
				<span class="liname">{{item.name}}</span>
			</div>
		</transition-group>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				menuarr: [{
						name: 'echarts1',
						id: 1
					},
					{
						name: 'echarts2',
						id: 2
					},
					{
						name: 'echarts3',
						id: 3
					},
					{
						name: 'echarts4',
						id: 4
					},
					{
						name: 'echarts5',
						id: 5
					},
				],
				prevIndex: 0
			}
		},
		mounted() {
			this.$nextTick(function() {
				window.addEventListener('scroll', this.onScroll)
			})
		},
		methods: {
			dragstart(value, item, e) {
				this.oldNum = value;
				e.dataTransfer.setData("infoName", " ");
			},
			// 做最终操作
			dragend(currentIndex, currentItem) {
				for (let i in this.menuarr) {
					if (this.menuarr[i].id == currentItem.id) {
						this.menuarr.splice(i, 1);
					}
				}
				// 在列表中目标位置增加新的节点
				this.menuarr.splice(0, 0, currentItem);

			},
		}
	}
</script>

<style>
	.flip-list-move {
		transition: transform 1s;
	}
</style>
